import {defaultLoader} from "../loaders/defaultLoader.js";

/**
 *     
 */
class CaseCreator {
    #loader;
    #stateVariable;
    #converter;
    #fulfilled;

    /**
     * @param loader 
     * @param converter  
     * @param stateVariable        
     * @param fulfilled ,    
     */
    constructor(loader = defaultLoader, converter = data => data, stateVariable = 'data', fulfilled = null) {
        this.#loader = loader;
        this.#converter = converter;
        this.#stateVariable = stateVariable;
        this.#fulfilled = fulfilled;
    }

    /**
     *   
     * @param loader 
     * @param converter  
     * @param stateVariable        
     * @param fulfilled ,    
     * @returns {CaseCreator}
     */
    static create = (loader = defaultLoader, converter = data => data, stateVariable = 'data', fulfilled = null) => {
        return new CaseCreator(loader, converter, stateVariable, fulfilled);
    };

    /**
     *   
     * @param loader  
     * @returns {CaseCreator}
     */
    setLoader = (loader) => {
        this.#loader = loader;
        return this;
    }

    /**
     *   
     * @param converter  
     * @returns {CaseCreator}
     */
    setConverter = (converter) => {
        this.#converter = converter;
        return this;
    }

    /**
     *      (store)     
     * @param stateVariable     (store)
     * @returns {CaseCreator}
     */
    setStateVariable = (stateVariable) => {
        this.#stateVariable = stateVariable;
        return this;
    }

    /**
     *  ,     
     * @param fulfilled
     * @returns {CaseCreator}
     */
    setFulfilled = (fulfilled) => {
        this.#fulfilled = fulfilled;
        return this;
    }

    /**
     *  
     * @returns {(function(*): void)|*}
     */
    build = () => {
        return (builder) => {
            builder
                .addCase(this.#loader.pending, (state) => {
                    state.isLoading = true;
                    state.isError = false;
                })
                .addCase(this.#loader.fulfilled, (state, action) => {
                    !this.#fulfilled ?
                        state[this.#stateVariable] = this.#converter(action.payload)
                        :
                        this.#fulfilled(state, action);
                    state.isLoading = false;
                })
                .addCase(this.#loader.rejected, (state) => {
                    state.isLoading = false;
                    state.isError = true;
                });
        }
    };
}

export default CaseCreator;