import SliceManager from "./sliceManager.js";
import storage from "redux-persist/lib/storage/index.js.flow";
import PersistSlice from "./persistSlice.js";
import {getTtlPersistTransform} from "./getTtlPersistTransform.js";

//   
const DEFAULT_TTL = 10; //TODO     

/**    */
class PersistSliceManager {
    #sliceManager;
    #config;
    #blackList = [];
    #key;
    #persistStorage;
    #transforms = [];
    #ttl;
    #transformBlackList = [];
    #clearValues = {};

    /**
     * @param name {string}  
     * @param initialState {{any}}  
     * @param config {{any}|null}  
     * @param blackList {[string]}  
     * @param customStorage   (  localstorage)
     * @param transforms {[{Transform<null|any, {data: *, updatedAt: number}, any, any>}]}   
     */
    constructor(name, initialState, config = null, blackList = [],
                customStorage = storage, transforms = []) {
        this.#sliceManager = new SliceManager(name, initialState, true);
        this.#config = config;
        this.#blackList = blackList;
        this.#persistStorage = customStorage;
        this.#transforms = transforms;
    }

    /**
     *
     * @param name {string}  
     * @param initialState {{any}|null}  
     * @param isApiResource {boolean} (  )
     * @returns {PersistSliceManager}
     */
    static create(name = "", initialState = null, isApiResource = true) {
        return new PersistSliceManager(name, initialState);
    }

    /**
     *  
     * @param config {{any}}  
     * @returns {PersistSliceManager}
     */
    setConfig = (config) => {
        this.#config = config;
        return this;
    };

    /**
     *    
     * @param key {string}   
     * @returns {PersistSliceManager}
     */
    setPersistKey = (key) => {
        this.#key = key;
        return this;
    };

    /**
     *    
     * @param customStorage   ,   localstorage
     * @returns {PersistSliceManager}
     */
    setPersistStorage = (customStorage) => {
        this.#persistStorage = customStorage;
        return this;
    };

    /**
     *      
     * @param blackList {[string]}      
     * @returns {PersistSliceManager}
     */
    setBlackList = (blackList) => {
        this.#blackList = blackList;
        return this;
    };

    /**
     *      
     * @param key {string}    
     */
    addToBlackList = (key) => {
        this.#blackList.push(key);
        return this;
    };

    /**
     *     
     * @param transforms {[{Transform<null|any, {data: *, updatedAt: number}, any, any>}]}  
     * @returns {PersistSliceManager}
     */
    setTransforms = (transforms) => {
        this.#transforms = transforms;
        return this;
    }

    /**
     *    
     * @param transform  
     * @returns {PersistSliceManager}
     */
    addToTransforms = (transform) => {
        this.#transforms.push(transform);
        return this;
    };

    /**
     *       
     * @param blackList    
     * @returns {PersistSliceManager}
     */
    setTransformBlackList = (blackList) => {
        this.#transformBlackList = blackList;
        return this;
    };

    /**
     *       
     * @param value    
     * @returns {PersistSliceManager}
     */
    addToTransformBlackList = (value) => {
        this.#transformBlackList.push(value);
        return this;
    };

    /**
     *      
     * @param ttl {bigint}   ( )
     * @returns {PersistSliceManager}
     */
    setTtl = (ttl) => {
        this.#ttl = ttl;
        return this;
    };

    /**
     *        
     * @returns {PersistSliceManager}
     */
    setDefaultTemporary = () => {
        this.#ttl = DEFAULT_TTL;
        return this;
    };

    /**
     *           
     * @param value {{key: value}}  
     * @returns {PersistSliceManager}
     */
    setClearValues = (value) => {
        this.#clearValues = value;
        return this;
    }

    /**
     *     
     * @param key {string}    
     * @param value {*}  
     * @returns {PersistSliceManager}
     */
    addClearValue = (key, value) => {
        this.#clearValues[key] = value;
        return this;
    }

    /**
     *   
     * @param name {string}   
     * @returns {PersistSliceManager}
     */
    setName = (name) => {
        this.#sliceManager.setName(name);
        return this;
    }

    /**
     *    
     * @param newState   
     * @returns {PersistSliceManager}
     */
    setInitialState = (newState) => {
        this.#sliceManager.setInitialState(newState);
        return this;
    };

    /**
     *   
     * @param key     (   )
     * @param value  
     * @returns {PersistSliceManager}
     */
    addState = (key, value) => {
        this.#sliceManager.addState(key, value);
        return this;
    };

    /**
     *  url
     * @param url api 
     * @returns {PersistSliceManager}
     */
    setUrl = (url) => {
        this.#sliceManager.setUrl(url)
        return this;
    };

    /**
     *   
     * @param name     (        )
     * @param action {function}   
     * @returns {PersistSliceManager}
     */
    addAction = (name, action) => {
        this.#sliceManager.addAction(name, action);
        return this;
    };

    /**
     *  
     * @param loader  
     * @returns {PersistSliceManager}
     */
    setLoader = (loader) => {
        this.#sliceManager.setLoader(loader);
        return this;
    };

    /**
     *   
     * @param cases   
     * @returns {PersistSliceManager}
     */
    addCases = (cases) => {
        this.#sliceManager.addCases(cases);
        return this;
    };

    /**
     *   
     * @returns {PersistSlice}
     */
    build = () => {
        const customSlice = this.#sliceManager.build();

        if (!this.#config) {
            //     
            if (!this.#transforms.length) {
                //         ,  ttl
                if (this.#transformBlackList.length || Object.keys(this.#clearValues).length) {
                    this.#ttl = DEFAULT_TTL;
                }
                //  ttl,    
                if (this.#ttl) {
                    this.#transforms = [getTtlPersistTransform(
                        this.#ttl, this.#transformBlackList, this.#clearValues)];
                }
            }

            //      
            const blackList = ['url', 'isLoading', 'isError'];

            //  
            this.#config = {
                key: this.#key ? this.#key : this.#sliceManager.getName(), //  
                storage: this.#persistStorage ? this.#persistStorage : storage, //  
                blacklist: this.#blackList.length ? [...this.#blackList, ...blackList] : blackList, //,      
                transforms: this.#transforms,
            }
        }

        return new PersistSlice(customSlice, this.#config);
    }
}

export default PersistSliceManager;